export function eventIdentify(user) {
  console.log("Identify got called");
  rudderanalytics.identify({
    name: user,
    category: "Identify User",
    email: `${user}@gmail.com`,
  });
}

export function eventPage(name, payload) {
  console.log("Calling Page");
  console.log(name);
  rudderanalytics.page(name, payload);
}

export function eventTrack(eventName, payload) {
  console.log(payload);
  console.log(eventName);
  rudderanalytics.track(eventName, payload);
  
}

export function clearRudderCache () {
  console.log("Sending organization data");
  const randomNumber = Math.floor(Math.random()*1000000);
  // rudderanalytics.reset(true);
  rudderanalytics.group("123456", {
    name: `MyGroup-${randomNumber}`,
    industry: "IT",
    employees: randomNumber,
    plan: "basic"
  })
}